export default {
	data() {
		return {
			isDisabled: true,
			list: [
				{
					id: "companyName",
					label: "发票抬头",
					plac: "请输入公司名称"
				},
				{
					id: "tfn",
					label: "纳税人识别号",
					plac: "(选填)"
				},
				{
					id: "email",
					label: "电子邮箱",
					plac: "(必填)"
				},
				{
					id: "regurl",
					label: "注册地址",
					plac: "(选填)"
				},
				{
					id: "regphone",
					label: "注册电话",
					plac: "(选填)"
				},
				{
					id: "bankName",
					label: "开户银行",
					plac: "(选填)"
				},
				{
					id: "bankAccount",
					label: "银行账号",
					plac: "(选填)"
				},
			],
			required: {
				companyName: "",
				email: ""
			}
		}
	},
	onLoad() {
		
	},
	methods: {
		formSubmit(e) {
			console.log(e.detail.value)
		},
		input(e) {
			var id = e.currentTarget.id;
			var ad = this.required;
			var _this = this;
			for(var key in ad){
				key == id ? ad[key] = e.detail.value : ad[key];
				if(ad[key] !== ""){
					_this.isDisabled = false;
				}
				for(var k in ad){
					if(ad[k] == ""){
						_this.isDisabled = true
					}
				}
			}
		}
	}
}
